<template>
  <div>
    <div class="row">
      <!-- 検索ボックス -->
      <div class="search-bar">
        <label for="typeSelect">検索：</label>
        <input type="text" v-model="searchQuery" placeholder="アイテムを検索" />
      </div>

      <!-- タイプ選択のドロップダウン -->
      <div class="select-box">
        <label for="typeSelect">タイプ：</label>
        <select id="typeSelect" v-model="selectedType">
          <option value="All">すべて</option>
          <option value="Attack">Attack</option>
          <option value="Balance">Balance</option>
          <option value="Cross">Cross</option>
          <option value="Guard">Guard</option>
          <option value="Rush">Rush</option>
          <!-- 他のタイプのオプションも追加 -->
        </select>
      </div>
    </div>

    <div class="row">
      <!-- Main選択のドロップダウン -->
      <div class="select-box">
        <label for="mainSelect">メインOP：</label>
        <select id="mainSelect" v-model="selectedMain">
          <option value="All">すべて</option>
          <option value="アビリティパワー">アビリティパワー</option>
          <option value="ブレイクパワー">ブレイクパワー</option>
          <option value="クリティカルパワー">クリティカルパワー</option>
          <option value="クリティカル率">クリティカル率</option>
          <option value="魔法防御力">魔法防御力</option>
          <option value="最大HP">最大HP</option>
          <option value="物理防御力">物理防御力</option>
          <option value="ねばり">ねばり</option>
          <option value="武器パワー">武器パワー</option>
          <!-- 他のMainのオプションも追加 -->
        </select>
      </div>

      <!-- Sub選択のドロップダウン -->
      <div class="select-box">
        <label for="subSelect">サブOP：</label>
        <select id="subSelect" v-model="selectedSub">
          <option value="All">すべて</option>
          <option value="アビリティパワー">アビリティパワー</option>
          <option value="ブレイクパワー">ブレイクパワー</option>
          <option value="クリティカルパワー">クリティカルパワー</option>
          <option value="クリティカル率">クリティカル率</option>
          <option value="魔法防御力">魔法防御力</option>
          <option value="最大HP">最大HP</option>
          <option value="物理防御力">物理防御力</option>
          <option value="ねばり">ねばり</option>
          <option value="武器パワー">武器パワー</option>
          <!-- 他のSubのオプションも追加 -->
        </select>
      </div>
    </div>
    <table>
      <thead>
        <tr>
          <th>アイコン</th>
          <th>アイテム名</th>
          <th>レベル</th>
          <th>タイプ</th>
          <th>効果</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in paginatedData" :key="item.Name">
          <td><img :src="item.Image" alt="Image" width="56" height="56" /></td>
          <td>
            <a @click="showDetails(item)">{{ item.Name }}</a>
          </td>
          <td>{{ item.level }}</td>
          <td>{{ item.type }}</td>
          <td>
            {{ item.Main_Option }}
            <br />
            {{ item.Sub_Option }}
          </td>
        </tr>
      </tbody>
    </table>

    <!-- ページネーションコントロール -->
    <div class="pagination-container">
      <div class="pagination">
        <button @click="prevPage" :disabled="currentPage === 1">前へ</button>
        <span>ページ {{ currentPage }} / {{ totalPages }}</span>
        <button @click="nextPage" :disabled="currentPage === totalPages">
          次へ
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    jsonData: Array,
  },
  data() {
    return {
      currentPage: 1,
      itemsPerPage: 25,
      searchQuery: "",
      selectedType: "All", // 選択されたタイプ
      selectedMain: "All", // 選択されたMain
      selectedSub: "All", // 選択されたSub
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    paginatedData() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.filteredData.slice(startIndex, endIndex);
    },
    filteredData() {
      return this.jsonData.filter(
        (item) =>
          item.Name.toLowerCase().includes(this.searchQuery.toLowerCase()) &&
          (this.selectedType === "All" || item.type === this.selectedType) &&
          (this.selectedMain === "All" ||
            item.Main_Option === this.selectedMain) &&
          (this.selectedSub === "All" || item.Sub_Option === this.selectedSub),
      );
    },
  },
  watch: {
    // currentPage プロパティの変更を監視
    currentPage: {
      handler: 'sortTable', // currentPage プロパティが変更されるたびに sortTable メソッドを呼び出す
      immediate: true, // コンポーネントがマウントされたときにも呼び出す
    },
    // selectedType, selectedMain, selectedSub の変更を監視
    selectedType: 'resetPageAndSort',
    selectedMain: 'resetPageAndSort',
    selectedSub: 'resetPageAndSort',
  },
  methods: {
    sortTable(column) {
      this.filteredData.sort((a, b) => {
        const aValue = a[column];
        const bValue = b[column];
        return aValue.localeCompare(bValue);
      });
    },
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    resetPageAndSort() {
      this.currentPage = 1;  // currentPage をリセット
      this.sortTable('Name'); // ソート処理を実行
    },
    showDetails(item) {
      // アイテム名をクリックしたときに詳細画面に遷移
      this.$router.push({ name: 'itemDetail', params: { itemName: item.Name } });
    },
  },
};
</script>

